<template>
  <div id="login">
    <div class="login-logo">
      <img src="../common/image/resume-logo.png" alt="logo" @click="setPath('/')">
    </div>
    <header class="login-header">Resume </header>
    <form @submit.prevent="login" class="login-form">
      <div class="login-info">
        <input type="text" v-model="formData.username" placeholder="">
        <input type="password" v-model="formData.password" placeholder="">
      </div>
      <div class="login-btn">
        <input type="submit" value="">
      </div>
    </form>
    <div class="signUp" @click="setPath('/signUp')"></div>
    <div class="forgetInfo">
      <div></div>
      <button>facebook</button>
      <button>google</button>
    </div>
    <footer class="footer">
      <p>RESUME</p>
      <p>
        <a href="javascript:void(0)"></a>
        <a href="javascript:void(0)"></a>
      </p>
    </footer>
  </div>
</template>

<script>
  import router from '../router/index'
  import store from '../store/index'
  import AV from '../lib/leancloud'
  import getAvUser from '../lib/getAvUser'
  import getErrorMessage from '../lib/getErrorMessage'

  export default {
    name: 'Login',
    data() {
      return {
        formData: {
          username: '',
          password: ''
        },
      }
    },
    methods: {
      login() { //
        AV.User.logIn(this.formData.username, this.formData.password).then(() => {
          this.$store.commit('SET_USER', getAvUser()) //vuex
          router.push('/layout') ///layout
          console.log('')
        }, (error) => {
          alert(getErrorMessage(JSON.parse(JSON.stringify(error)).code))
        })
      },
      setPath(path) {
        router.push(path)
      }
    }
  }

</script>

<style lang="less" scoped>
  @import url('../common/style/login.less');
</style>



