<template>
  <div id="signUp">
    <div class="signUp-logo">
      <img src="../common/image/resume-logo.png" alt="logo" @click="setPath('/')">
    </div>
    <header class="signUp-header">Resume</header>
    <form @submit.prevent="signUp" class="signUp-form">
      <div class="signUp-info">
        <input type="text" v-model="formData.username" placeholder="">
        <input type="password" v-model="formData.password" placeholder="">
      </div>
      <div class="signUp-btn">
        <input type="submit" value="">
      </div>
    </form>
    <div class="login">?
      <a href="javascript:void(0)" @click.prevent="setPath('/login')"></a>
    </div>
    <footer class="footer">
      <p>RESUME</p>
      <p>
        <a href="javascript:void(0)"></a>
        <a href="javascript:void(0)"></a>
      </p>
    </footer>
  </div>
</template>

<script>
  import router from '../router/index'
  import store from '../store/index'
  import AV from '../lib/leancloud'
  import getAvUser from '../lib/getAvUser'
  import getErrorMessage from '../lib/getErrorMessage'
  export default {
    name: 'signUp',
    data() {
      return {
        formData: {
          username: '',
          password: ''
        },
      }
    },
    methods: {
      signUp() { //
        var user = new AV.User()
        user.setUsername(this.formData.username)
        user.setPassword(this.formData.password)
        user.signUp().then((loginedUser) => {
          this.$store.commit('SET_USER', getAvUser()) //vuex
          router.push('Layout')
          console.log('')
        }, (error) => {
          alert(getErrorMessage(error))
        })
      },
      setPath(path) {
        router.push(path)
      }
    },
  }

</script>

<style lang="less" scoped>
  @import url('../common/style/signUp.less');

</style>



