<template>
  <div id="home">
    <header class="header" v-if="user.id">
      <button @click.prevent="logOut"></button>
      <span>{{user.username}}</span>
    </header>
    <header class="header" v-else>
      <a href="javascript:void(0)" @click="setRouter('/login')"></a>
      <a href="javascript:void(0)" @click="setRouter('/signUp')"></a>
    </header>
    <main class="main">
      <div class="main-logo">
        <div>
          <img src="../common/image/resume-logo.png" alt="logo">
        </div>
        <div>Resume</div>
      </div>
      <div class="content-one"></div>
      <div class="content-two"></div>
      <div class="word"></div>
      <div class="btn" @click="setRouter('/layout')">Resume</div>
    </main>
  </div>
</template>

<script>
  import router from '../router/index'
  import AV from 'leancloud-storage'
  import getAvUser from '../lib/getAvUser'

  export default {
    name: 'Home',
    methods: {
      setRouter(path) {
        router.push(path)
      },
      logOut() {
        AV.User.logOut();
        this.$store.commit('SET_USER', getAvUser())
        window.location.reload()
      },
    },
    computed: {
      user() {
        return this.$store.state.user
      }
    }
  }

</script>
<style lang="less" scoped>
  @import url('../common/style/home.less');

</style>



